import Select, { MultiValue, SingleValue } from 'react-select';
import { AbitAdmissionCondition } from "../models/abit";
import { GUChecks, GUSets } from "../models/common";
import { ApplsChecksVals } from "../ts/applications";
import { sspvo_statuses } from "../ts/getBigFuncs";

export interface IOption<T> {
    value: T;
    label: string;
    id: number;
    isFixed?: boolean;
}

export type IMultiSelect<T> = MultiValue<IOption<T>>;
export type ISingleSelect<T> = SingleValue<IOption<T>>;

interface MSP<T = string> {
    handler: (e: IMultiSelect<T>) => void;
    defOpt: T[];
}

interface MultiSelProps<T> extends MSP<T> {
    selectOptions: IMultiSelect<any>;
}

interface SSP<T = string> {
    handler: (e: ISingleSelect<T>) => void;
    defOpt?: T;
}

interface SingleSelProps<T> extends SSP<T> {
    selectOptions: IMultiSelect<any>;
}

const mapFunc = ([value, label]: [string, string], id: number) => ({ value, label, id });

function MultiSelGen<T>({ handler, selectOptions, defOpt }: MultiSelProps<T>) {
    const defautlOption = selectOptions.filter(o => defOpt.includes(o.value));

    return <div className="msel">
        <Select
            defaultValue={defautlOption}
            isMulti
            options={selectOptions}
            className="basic-multi-select"
            classNamePrefix="select"
            onChange={handler} />
    </div>;
}

export const MultiSelect = {
    ApplsChecks: <T,>(props: MSP<T>) => MultiSelGen<T>({
        ...props,
        selectOptions: ApplsChecksVals.map((o, id) => ({ ...o, id }))
    }),

    GUStatuses: <T,>(props: MSP<T>) => MultiSelGen<T>({
        ...props,
        selectOptions: sspvo_statuses.map(s => ({ value: s.code, label: s.name, id: s.id }))
    }),

    AbitCondition: <T,>(props: MSP<T>) => MultiSelGen<T>({
        ...props,
        selectOptions: Object.entries(AbitAdmissionCondition).map(mapFunc)
    }),

    GUChecks: <T,>(props: MSP<T>) => MultiSelGen<T>({
        ...props,
        selectOptions: Object.entries(GUChecks).map(mapFunc)
    }),

    GUSets: <T,>(props: MSP<T>) => MultiSelGen<T>({
        ...props,
        selectOptions: Object.entries(GUSets).map(mapFunc)
    })
};

function SingleSelGen<T>({ handler, selectOptions, defOpt }: SingleSelProps<T>) {
    const defautlOption = selectOptions.filter(o => defOpt === o.value);

    return <div className="msel">
        <Select
            defaultValue={defautlOption}
            options={selectOptions}
            className="basic-single"
            classNamePrefix="select"
            onChange={handler} />
    </div>;
}

export const SingleSelect = {
    ApplsChecks: <T,>(props: SSP<T>) => SingleSelGen<T>({
        ...props,
        selectOptions: ApplsChecksVals.map((o, id) => ({ ...o, id }))
    }),

    GUStatuses: <T,>(props: SSP<T>) => SingleSelGen<T>({
        ...props,
        selectOptions: sspvo_statuses.map(s => ({ value: s.code, label: s.name, id: s.id }))
    }),

    AbitCondition: <T,>(props: SSP<T>) => SingleSelGen<T>({
        ...props,
        selectOptions: Object.entries(AbitAdmissionCondition).map(mapFunc)
    }),

    GUChecks: <T,>(props: SSP<T>) => SingleSelGen<T>({
        ...props,
        selectOptions: Object.entries(GUChecks).map(mapFunc)
    }),

    GUSets: <T,>(props: SSP<T>) => SingleSelGen<T>({
        ...props,
        selectOptions: Object.entries(GUSets).map(mapFunc)
    })
};