import { RSSn } from "../models/common";

interface IBaseSelectProps {
    value: number;
    handler: (e: React.ChangeEvent<HTMLSelectElement>) => void;
}

interface ISimpleSelectProps extends IBaseSelectProps {
    arr: number[];
}

const SS: React.FC<ISimpleSelectProps> = ({ arr, value, handler }) => {
    return <select value={value} onChange={handler} className="msel numi">
        {arr.map(c => <option key={c} value={c}>{c}</option>)}
    </select>;
};

interface SimpleSelectNumericProps extends Omit<ISimpleSelectProps, "handler"> {
    setFunc: RSSn;
}

export const SSN: React.FC<SimpleSelectNumericProps> = ({ arr, value, setFunc }) => <SS arr={arr} value={value} handler={e => setFunc(Number(e.target.value))} />;

interface INumSelProps {
    value: number;
    setFunc: RSSn;
}

export const SimpleSelect = {
    Save: (props: INumSelProps) => <label className="ssel">Сохранять каждые <SSN arr={[10, 20, 30, 50, 100]} {...props} /> человек</label>,
    Split: (props: INumSelProps) => <label className="ssel">Разбить по <SSN arr={[1, 2, 4, 5, 10, 15]} {...props} /> элементов</label>,
    Limit: (props: IBaseSelectProps) => <label className="ssel">Лимит <SS arr={[5, 10, 15, 25, 50, 100]} {...props} /> элементов</label>
};

interface ICheckBoxProps {
    id: string;
    text: string;
    check?: boolean;
    handler: (e: React.ChangeEvent<HTMLInputElement>) => void;
}

export const CheckBox: React.FC<ICheckBoxProps> = ({ id, text, check, handler }) => <p className="ch"><input type="checkbox" id={id} checked={check} onChange={handler} /> {text}</p>;