import { DS } from "./common";

/**
 * @copyright kazakovstepan, 2022
 */
export type AbitAdmissionConditionVals = "without_entry_tests" | "on_state_line" | "general_competition" | "by_unusual_quota" | "by_target_quota" | "by_special_quota";
export type AbitFinancingVals = "budget" | "contract";
export interface SelectedProgram {
    id: number;
    title: string;
    direction_code: string;
    admission_condition: AbitAdmissionConditionVals;
    financing: AbitFinancingVals;
}

export const AbitAdmissionCondition = {
    without_entry_tests: "БВИ",
    general_competition: "Общий конкурс",
    by_unusual_quota: "Льгота",
    by_target_quota: "Целевое",
    by_special_quota: "Спец Квота",
    on_state_line: "ГосЛиния"
};

export interface Window {
    id: number;
    title: string;
}

interface CompetitiveGroup {
    id: number;
    degree: string;
    study_form: string;
    title: string;
    budget_min: number;
    contract: number;
    year: number;
    disciplines: string[];
    target_reception: number;
    invalid: number;
    special: number;
}

export interface AgreementSelectedProgram {
    id: number;
    priority: number;
    program?: string;
    competitive_group: CompetitiveGroup;
    financing: AbitFinancingVals;
    admission_condition: AbitAdmissionConditionVals;
    file?: any;
    discipline?: any;
    dissertation_topic?: any;
    faculty?: any;
    teacher?: any;
    language?: any;
    specialty_exam?: any;
    language_exam?: any;
    supervisor?: any;
    applications: Application[];
    quota_file?: any;
    degree?: any;
}

interface Agreement {
    financing: AbitFinancingVals;
    student_agreement_file?: any;
    verified_agreement_file: string;
    selected_program: AgreementSelectedProgram;
    selected_program_id: number;
    edited_by_window?: any;
}

interface User {
    id: number;
    degree: string;
    student_details_id: number;
    first_name: string;
    last_name: string;
    middle_name: string;
    email: string;
    passport_number: string;
    birth_date: DS;
    is_compatriot?: boolean;
    phone: string;
    is_send_original: boolean;
    is_send_original_updated_at?: DS;
    is_sspvo: boolean;
}

interface Application {
    id: number;
    status: string;
    window: Window;
    agreement: Agreement;
    send_agreement: boolean;
    case_formed: boolean;
    user: User;
    number: string;
    created_at: DS;
    updated_at: DS;
}

interface FullName {
    first_name: string;
    last_name: string;
    middle_name: string;
}

interface Contact {
    email: string;
    phone: string;
}

interface PersonId {
    division_name: string;
    type: string;
    series: string;
    number: string;
    issued_date: DS;
    validity_period?: any;
    division_code: string;
    scans: string[];
    category: string;
}

interface Snils {
    category: string;
    number: string;
    scans: string[];
}

interface Documents {
    person_id: PersonId;
    is_have_old_passport: boolean;
    passport_old?: {
        category: string;
        series: string;
        number: string;
    };
    type: string;
    snils?: Snils;
}

export interface Personally {
    full_name_old_date?: any;
    full_name: FullName;
    full_name_old?: any;
    contact: Contact;
    is_have_old_name: boolean;
    citizenship: string;
    birth_date: DS;
    birth_place: string;
    gender: string;
    documents: Documents;
    compatriot_file?: any;
    is_compatriot?: any;
}

interface Address {
    country: string;
    region: string;
    area: string;
    city: string;
    locality?: any;
    street: string;
    house: string;
    building?: any;
    flat?: any;
    postcode: string;
    registration_address?: any;
    specified_manually: boolean;
    need_dormitory_for_exams: boolean;
    need_dormitory_for_study?: any;
}

interface DocumentEducation {
    scans: string[];
    supplements: string[];
    code?: any;
    series?: any;
    number: string;
    issued_date: DS;
    is_graduate2015: boolean;
    type?: any;
    category: string;
}

interface University {
    id: number;
    name: string;
    city_id: number;
    city: string;
    region_id: number;
    region: string;
}

export interface Education {
    country: string;
    year: number;
    first_time_degree: boolean;
    document_education: DocumentEducation;
    document: string;
    university: University;
}

export interface Exam {
    scores: number;
    year: number;
    date?: DS;
    is_appeal?: boolean;
    snils?: number;
    case_number?: number;
    type: string;
    discipline: string;
}

interface EntranceTests {
    date: DS;
    type: string;
    discipline: string;
}

interface Exams {
    exams: Exam[];
    entrance_tests: EntranceTests[];
}

interface Medical {
    certificate: string;
    fluorography: string;
    vaccinations: string[];
    policy: string;
}

interface Privilege {
    privilege_category: string;
    type: string;
    name: string;
    date: {
        indefinitely: boolean;
        issue: DS;
        term: DS;
    };
    scans: string[];
}

interface Privileges {
    privileges: Privilege[];
    advantages: any[];
    is_special_conditions: boolean;
    special_conditions_dictionary?: any;
    is_have_privileges: boolean;
    is_have_advantages: boolean;
}

interface IsuOlympiad {
    name: string;
    profile: string;
    diploma: string;
    year: number;
}

interface RealOlympiad {
    isu_olympiad: IsuOlympiad;
    grade: number;
    certificate: string;
    scans: string[];
}

interface Olympiads {
    is_have_olympiads: boolean;
    olympiads: RealOlympiad[];
}

export interface Achievement {
    type: string;
    scans: string[];
    award?: any;
    certificate?: any;
    grant?: any;
    olympiad?: any;
    patent?: any;
    publication?: any;
    professional?: any;
    scores: number;
}

interface Achievements {
    achievements: Achievement[];
    is_have_achievements: boolean;
}

export interface IRealAbit {
    selected_programs: SelectedProgram[];
    user_sspvo_id?: number;
    student_details_sspvo_id?: number;
    application: Application;
    personally: Personally;
    address?: Address;
    photo?: any;
    education: Education;
    exams: Exams;
    relatives?: any;
    medical?: Medical;
    military?: any;
    privileges: Privileges;
    olympiads: Olympiads;
    achievements: Achievements;
}

export interface IRealJSON {
    message: string;
    meta_data: {
        total: number;
    };
    ok: boolean;
    result: {
        bachelors: IRealAbit[];
    };
}

